import React from 'react';
import styled from 'styled-components';
import Icon from "../Icon";
import {useRipple} from "../Hooks";

const Base = styled.div`
    box-sizing: border-box;
    line-height: 0;
    transition: 0.3s ease transform, 0.3s ease background-color;
    transform: ${({active}) => active ? 'rotateZ(-90deg)' : 'none'};
    background-color: ${({active}) => active ? 'rgba(0, 0, 0, 0.25)' : 'transparent'};
    overflow: hidden;
    position: relative;
    padding: 12px;
    border-radius: 50%;
`;

const Hamburger = ({onClick, active}) => {
    const [ref, ripples] = useRipple();

    return (
        <Base ref={ref} active={active} onClick={onClick}>
            {ripples}
            <Icon>
                menu
            </Icon>
        </Base>
    )
};

export default Hamburger;