import React from 'react';
import styled from 'styled-components';
import Button from "../Atomic/Button";
import Input from "../Atomic/Input";

const Text = styled.div`
    padding: 16px;
`;

const Calculation = ({children, solution}) => {
    const [state, setState] = React.useState('');

    return (
        <>
            <Text>
                {children}
            </Text>
            <Text>
                <form onSubmit={(event) => {
                    solution(parseFloat(state));
                    event.preventDefault();
                }}>
                    <Input placeholder={'Lösung'} value={state} onChange={(event) => setState(event.target.value)} />
                </form>
            </Text>
            <div style={{padding: 16}}>
                <Button onClick={() => solution(parseFloat(state))}>Rechnung abgeben</Button>
            </div>
        </>
    )
};

export default Calculation;