import React from 'react';
import Progress from "../Atomic/Progress";
import CardOverview from "../Atomic/CardOverview";
//import {useParams} from 'react-router-dom';
import {useModule} from "../Context/CourseContext";
import {useUser} from "../Context/UserContext";
import MultipleChoice from "../Exercises/MultipleChoice";
import Theory from "../Exercises/Theory";
import Calculation from "../Exercises/Calculation";
import Slider from "../Atomic/Slider";

const Exercise = ({...props}) => {
    //const {courseId, groupId ,moduleId} = useParams();

    const correctAnswer = (solution) => {
        if(solution !== module[user.progress].solution) return;

        next();
    };

    const next = () => {
        setUser(state => ({
            ...state,
            progress: state.progress + 1,
        }));
    }

    const renderTheory = ({theory}) => {
        return (
            <Theory next={next}>
                {theory}
            </Theory>
        )
    };

    const renderMultipleChoice = ({question, choices}) => {
        return (
            <MultipleChoice choices={choices} solution={correctAnswer}>
                {question}
            </MultipleChoice>
        )
    };

    const renderCalculation = ({question}) => {
        return (
            <Calculation solution={correctAnswer}>
                {question}
            </Calculation>
        )
    };

    const renderFinished = () => {
        return (
            <>
                <Progress progress={1} />
                <CardOverview title={'Ende Demo'}>
                    <div style={{padding: 16}}>
                        Aufgaben gelöst! <br /><br />
                        <Slider onClick={() => setUser(state => ({...state, progress: 0}))}>Demo zurücksetzen</Slider>
                    </div>
                </CardOverview>
            </>
        )
    }

    const switchType = (type) => {

        switch(module[user.progress].type) {
            case 'theory':
                return renderTheory(module[user.progress]);
            case 'multiple-choice':
                return renderMultipleChoice(module[user.progress]);
            case 'calculation':
                return renderCalculation(module[user.progress]);
            default:
                return (<div>Ups uns ist da ein Fehler unterlaufen.</div>)
        }
    }

    const module = useModule('Gleichstromtechnik', 'Grundlagen', 'Strom, Spannung und Widerstand');

    const [user, setUser] = useUser();

    const renderNormal = () => (
        <>
            <Progress progress={user.progress / module.length} />
            <CardOverview title={module[user.progress].title}>
                {switchType(module[user.progress].type)}
            </CardOverview>
        </>
    );

    if(module.length > user.progress) {
        return renderNormal();
    } else {
        return renderFinished();
    }
}

export default Exercise;