import React from 'react';
import styled from 'styled-components';
import Fader from "../Atomic/Fader";
import Hamburger from "../Atomic/Hamburger";

const Base = styled.div`
    position: sticky;
    top: 0;
    left: 0;
    z-index: 900;
`;

const Container = styled.div`
    color: ${props => props.theme.primary.color};
    background-color: ${props => props.theme.primary.main};
    padding: 4px 16px;
    display: flex;
    align-items: center;
    
    ${props => props.theme.elevation[6]}
`;

const NavBar = ({sideNavHandle, active}) => {
    return (
        <Base>
            <Container>
                <Hamburger onClick={sideNavHandle} active={active} />
                <Fader>eLearning App</Fader>
            </Container>

        </Base>
    )
};

export default NavBar;