export const useModule = (courseId, groupId, moduleId) => {
    const module = React.useContext(CourseContext);

    return module[courseId].children[groupId].children[moduleId].children;
}

export const CourseProvider = ({children}) => {
    const [courses] = React.useState(coursesData);

    return (
        <CourseContext.Provider value={courses}>
            {children}
        </CourseContext.Provider>
    )
};