import React from 'react';
import styled from 'styled-components';
import {useDrag} from 'react-use-gesture';
import {useSpring, animated} from 'react-spring';
import Icon from "../Icon";

const Base = animated(styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: ${props => props.theme.primary.dark};
    color: transparent;
    border-radius: 4px;
    width: 200px;
    margin: 0 auto;
`);

const Animated = animated(styled.div`
    text-align: center;
    width: 200px;
    display: inline-block;
    padding: 16px;
    color: ${props => props.theme.primary.color};
    background-color: ${props => props.theme.primary.main};
    border-radius: 4px;
    ${props => props.theme.elevation[3]}
`);

const StyledIcon = styled(Icon)`
    color: white;
    position: absolute;
`;

const Slider = ({children, onClick}) => {
    const base = React.useRef();
    const anim = React.useRef();

    const [{x}, set] = useSpring(() => ({x: 0}));

    const bind = useDrag(({down, movement}) => {
        set({x: down && movement[0] > 0 ? movement[0] : 0});
        if(movement[0] > base.current.offsetWidth) onClick();
    });

    return (
        <Base ref={base}>
            <StyledIcon>send</StyledIcon>
            <Animated ref={anim} {...bind()} style={{transform: x.interpolate(x => `translateX(${x}px)`)}}>
                {children}
            </Animated>

        </Base>
    )
};

export default Slider;
