import React from 'react';
import Input from "../Atomic/Input";
import {useUser} from "../Context/UserContext";
import CardOverview from "../Atomic/CardOverview";
import styled from 'styled-components';

const List = styled.div`

`;

const Item = styled.div`
    padding: 0px 16px;
    padding-bottom: 16px;
`;

const Profile = ({...props}) => {
    const [user, setUser] = useUser();

    const updateField = (event, field) => {
        const value = event.target.value;

        switch(field) {
            case 'fullName':
                setUser(state => ({...state, fullName: value}));
                console.log(value, field);
                break;
            case 'nickName':
                setUser(state => ({...state, nickName: value}));
                console.log(value, field);
                break;
            case 'email':
                setUser(state => ({...state, email: value}));
                console.log(value, field);
                break;
            default:
                return;
        }
    }

    return (
        <CardOverview title={'Profil'}>
            <List>
                <Item>
                    Name
                    <Input value={user.fullName} onChange={(event) => updateField(event,'fullName')}/>
                </Item>
                <Item>
                    Nickname
                    <Input value={user.nickName} onChange={(event) => updateField(event,'nickName')}/>
                </Item>
                <Item>
                    Email Adresse
                    <Input value={user.email} onChange={(event) => updateField(event,'email')}/>
                </Item>
            </List>
        </CardOverview>
    )
};

export default Profile;