import React from 'react';
import styled from 'styled-components';
import {CourseContext} from "./Context/CourseContext";
import Module from "./Atomic/Module";

const Carousel = styled.div`
    width: 100%;
    display: flex;
    overflow: auto;
    padding: 16px;
    box-sizing: border-box;
`;

const CourseOverview = props => {
    const courses = React.useContext(CourseContext);

    const constructCourseCards = (courses) => {
        let courseCards = [];
        for (let key in courses) {
            courseCards.push(
                <Module key={key} to={`/course/${key}`} title={key} pictureUrl={courses[key].pictureUrl} />
            )
        }

        return courseCards;
    };

    return (
        <Carousel>
            {constructCourseCards(courses)}
        </Carousel>
    )
};

export default CourseOverview;