import React from 'react';
import styled from 'styled-components';
import Button from "../Atomic/Button";
import Radio from "../Atomic/Radio";

const Text = styled.div`
    padding: 16px;
`;

const Form = styled.form`
    padding: 16px;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
`;

const MultipleChoice = ({children, choices, solution}) => {
    const [state, setState] = React.useState(0);

    const constructAnswers = () => {
        const temp = [];

        for(let i = 0; i < choices.length; i++) {
            temp.push(<Radio key={i} handleChange={handleChange} value={i} checked={state === i}>{choices[i]}</Radio>)
        }

        return temp;
    };

    const handleChange = (event) => {
        setState(parseInt(event.target.value));
    };

    return (
        <>
            <Text>
                {children}
            </Text>
            <Form>
                {constructAnswers()}
            </Form>
            <div style={{padding: 16}}>
                <Button onClick={() => solution(state)}>Lösung abgeben</Button>
            </div>
        </>
    )
};

export default MultipleChoice;