import React from 'react';
import CourseOverview from "../CourseOverview";
import CardOverview from "../Atomic/CardOverview";

const Start = props => {
    return (
        <>
            <CardOverview title={'Mein Kurs'}>
                <div style={{padding: 16}}>
                    Hier steht bald dein zuletzt bearbeiteter Kurs.
                </div>
            </CardOverview>
            <CardOverview title={'Elektrotechnik Kurse'}>
                <CourseOverview />
            </CardOverview>
            <CardOverview title={'Leaderboard'}>
                <div style={{padding: 16}}>
                    Hier steht bald ein Leaderboard.
                </div>
            </CardOverview>
        </>
    )
};

export default Start;