import React from 'react';
import {useSpring} from "react-spring";
import Container from "./Components/Container";
import Shader from "./Components/Shader";
import List from "./Components/List";
import ListItem from "./Components/ListItem";
import Avatar from "./Components/Avatar";
import DoubleLineItem from "./Components/DoubleLineItem";
import {useUser} from "../Context/UserContext";
import {withTheme} from "styled-components";
import {useAuth} from "../Context/AuthProvider";
import {Link} from "react-router-dom";

const SideNav = ({active, sideNavHandle, theme}) => {
    const {opacity, transform} = useSpring({
        opacity: active ? 1 : 0,
        transform: active ? 0 : -310
    });

    const [user] = useUser();

    // eslint-disable-next-line no-unused-vars
    const [login, logout] = useAuth();

    return (
        <>
            <Container style={{
                transform: transform.interpolate(x => `translateX(${x}px)`),
                visibility: opacity.interpolate(x => x > 0 ? 'visible' : 'hidden')
            }}>
                <List>
                    <Link to={{pathname: '/profile', state: 'Profil'}} style={{textDecoration: 'none'}}>
                        <ListItem style={{backgroundColor: theme.primary.main, paddingBottom: 24, paddingTop: 24}}>
                                <Avatar>{user.initials}</Avatar>
                                <DoubleLineItem subtitle={user.email}>
                                    {user.fullName}
                                </DoubleLineItem>
                        </ListItem>
                    </Link>
                    <Link style={{textDecoration: 'none', color: 'black'}} to={{pathname: '/settings', state: 'Einstellungen'}}>
                        <ListItem>
                            Einstellungen
                        </ListItem>
                    </Link>
                    <ListItem onClick={logout}>
                        Logout
                    </ListItem>
                </List>
            </Container>
            <Shader onClick={() => sideNavHandle(false)} style={{
                opacity: opacity.interpolate((x => x)),
                visibility: opacity.interpolate((x) => x > 0 ? 'visible' : 'hidden')
            }}/>
        </>
    );
};

export default withTheme(SideNav);