import React from 'react';
import styled, {withTheme} from 'styled-components';
import {useRipple} from "../Hooks";

const Label = styled.label`
    display: inline-block;
    padding: 8px;
    line-height: 0;
    position: relative;
    font-size: 10pt;
`;

const Flex = styled.div`
    display: flex;
    align-items: center;
`;

const OuterCircle = styled.div`
    border-radius: 50%;
    width: 8px;
    height: 8px;
    border: 2px solid ${props => props.theme.secondary.main};
    background-color: transparent;
    padding: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 16px;
    ${props => props.theme.elevation[1]}
`;

const InnerCircle = styled.div`
    transition: 0.3s ease transform;
    background-color: ${props => props.theme.secondary.main};
    border-radius: 50%;
    width: 8px;
    height: 8px;
    transform: scale(0);
`;

const Input = styled.input`
    visibility: hidden;
    width: 0;
    height: 0;
    margin: 0;
    
    :checked + div > div {
        transform: scale(1);
    }
`;

const Radio = ({children, theme, handleChange, value, checked, ...props}) => {
    const [ref, ripples] = useRipple({duration: 500, color: theme.secondary.main + 'AF', size: 5}, [11, 11], 'change');

    return (
        <Label ref={ref}>
            {ripples}
            <Flex>
                <Input value={value} onChange={handleChange} type={'radio'} checked={checked}/>
                <OuterCircle>
                    <InnerCircle />
                </OuterCircle>
                {children}
            </Flex>
        </Label>
    )
};

export default withTheme(Radio);