import React from 'react';
import styled from 'styled-components';
import {Link} from "react-router-dom";
import {useRipple} from "../Hooks";

const Title = styled.div`
    padding: 16px;
    padding-bottom: 0;
    color: #666;
    font-size: 8pt;
`;

const Picture = styled.div`
    background: url(${props => props.pictureUrl}) center center / cover;
    height: 64px;
    width: 64px;
    overflow: hidden;
    border-radius: 50%;
`;

const Item = styled(Link)`
    -webkit-tap-highlight-color: transparent;
    outline: none !important;
    flex: 1 1 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
    text-decoration: none;
    min-width: 120px;
`;

const Progress = styled.div`
    border-radius: 50%;
    padding: 2px;
    border: 2px solid ${props => props.theme.secondary.main};
    ${props => props.theme.elevation[3]}
`;

const Module = ({pictureUrl, title, to, disabled}) => {
    const [ref, ripples] = useRipple();

    return (
        <Item ref={ref} to={to} onClick={(event) => {
            if(disabled) event.preventDefault();
        }}>
            {ripples}
            <Progress>
                <Picture pictureUrl={pictureUrl} />
            </Progress>
            <Title>
                {title}
            </Title>
        </Item>
    )
};

export default Module;