import React from 'react';
import NavBar from "./NavBar";
import BottomBar from "./BottomBar";
import SideNav from "../SideNav";

const UI = ({...props}) => {
    const [state, setState] = React.useState({sideNav: false});

    const toggleSideNav = (open = !state) => {
        setState({
            ...state,
            sideNav: open,
        });
    }

    return (
        <>
            <SideNav active={state.sideNav} sideNavHandle={toggleSideNav} />
            <NavBar active={state.sideNav} sideNavHandle={toggleSideNav} />
            <BottomBar/>
        </>
    )
};

export default UI;